import React, { useEffect, useState } from 'react'

const Home = props => {
    const [tales, setTales] = useState([])
    useEffect(() => {
        fetch('/static/list.json')
        .then(res => res.json())
        .then(res => {
            console.log(res)
            setTales(res.tales)
        })
    }, [])
    return (
        <div>
            <ul>
            {
                tales.map (t => <li>
                    <a href={`/read/s/${t.id}`}>{t.name}</a>
                </li>)
            }
            </ul>
        </div>
    )
}

export default Home