import { URL_LOGIN, URL_SIGNUP } from '../../utils/urls'
import { USER_UPDATE } from '../actionTypes'
import axios from 'axios'

export const actionLogin = user => {
    const { email, pswd } = user
    return dispatch => {
        axios.post(URL_LOGIN, { email, pswd })
        .then(res => {
            let r = res.data
            if(r.status === 200) {
                let user = r.data
                dispatch({
                    type: USER_UPDATE, 
                    payload: { user }
                })
            }
            else {
                alert('Sign in failed...')
            }
        })
        .catch(err => {
            console.log(err)
            alert('Sign in failed...')
        })
    }
}

export const actionSignup = user => {
    const { email, pswd, firstname, lastname } = user
    return dispatch => {
        axios.post(URL_SIGNUP, { email, pswd, firstname, lastname })
        .then(res => {
            let r = res.data
            if(r.status === 200 || r.status === 1152) {
                let user = r.data
                dispatch({
                    type: USER_UPDATE, 
                    payload: { user }
                })
            }
            else {
                alert('Sign up failed...')
            }
        })
        .catch(err => {
            console.log(err)
            alert('Sign up failed...')
        })
    }
}