import React, { useState } from 'react'
import Container from '@material-ui/core/Container'
import Box from '@material-ui/core/Box'
import Grid from '@material-ui/core/Grid'
// import Paper from '@material-ui/core/Paper'
// import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import { connect } from 'react-redux'
import ExportTaleModal from './ExportTaleModal'
import { URL_CREATE_TALE } from '../../../utils/urls'
import { InfoCard } from '../../common/cards'
import featureText from './exportFeatureText'
import { Li } from '../../common/misc'

function download (filename, text) {
    var element = document.createElement('a');
    element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
    element.setAttribute('download', filename);

    element.style.display = 'none';
    document.body.appendChild(element);

    element.click();

    document.body.removeChild(element);
}

const CreateTaleFinish = props => {
    const { wipTale } = props
    const [isExpTaleModalVisible, setExpTModalVis] = useState(false)
    const [expTale, setExpTale] = useState('')
    const getWIPTale = () => {
        const { storyboard, ...tale } = wipTale
        return tale
    }

    const exportWIPTale = () => {
        const tale = getWIPTale()
        console.log(tale)
        const content = btoa(encodeURIComponent((JSON.stringify(tale))))
        download(`${tale.info.name.replace(/\s+/g, '-')}-${tale.id}.rtl`, content)
        setExpTale(content)
        setExpTModalVis(true)
    }

    const saveTaleLocal = () => {
        const tale = getWIPTale()
        const allReactales = JSON.parse(window.localStorage.getItem("allReactales")) || {}
        allReactales[tale.id] = tale
        window.localStorage.setItem("allReactales", JSON.stringify(allReactales))
        alert("Saved succesfully. " + localStorageSpace())
    }

    const saveTaleOnline = () => {
        const tale = getWIPTale()
        // console.log(tale)
        fetch(URL_CREATE_TALE,
        {
            method: "POST",
            body: JSON.stringify(tale)
        })
        .then(res => res.json())
        .then(res => console.log(res))
    }

    const localStorageSpace = () => {
        var allStrings = '';
        for (var key in window.localStorage) {
            if (window.localStorage.hasOwnProperty(key)) {
                allStrings += window.localStorage[key];
            }
        }
        return allStrings ? 3 + ((allStrings.length * 16) / (8 * 1024)) + ' KB' : 'Empty (0 KB)';
    };

    return (
        <Box pt={3} pb={5}>
            <ExportTaleModal 
                isOpen = {isExpTaleModalVisible}
                handleClose = {() => setExpTModalVis(false)}
                expTaleContent = {expTale}
            />
            <Container maxWidth="md">
                <Grid container spacing={2}>
                    <Grid item xs={12} sm={4}>
                        <InfoCard>
                            <h2 className="margin-bottom-0">Export Reactale</h2>
                            {featureText.exportTale.desc}

                            <h3 className="margin-bottom-0"><u>Pros &amp; Cons</u></h3>
                            <ul className="fillmid">
                                {
                                    featureText.exportTale.features.map( t => 
                                        <Li key={t}>{t}</Li>
                                    )
                                }
                            </ul>

                            <Button
                                variant="outlined"
                                color="primary"
                                onClick={exportWIPTale}
                            >
                                Export Reactale
                            </Button>
                        </InfoCard>
                    </Grid>
                    <Grid item xs={12} sm={4}>
                        <InfoCard>
                            <h2 className="margin-bottom-0">Save Offline</h2>
                            {featureText.saveLocal.desc}

                            <h3 className="margin-bottom-0"><u>Pros &amp; Cons</u></h3>
                            <ul className="fillmid">
                                {
                                    featureText.saveLocal.features.map( t => 
                                        <Li key={t}>{t}</Li>
                                    )
                                }
                            </ul>

                            <Button
                                variant="outlined"
                                color="primary"
                                onClick={saveTaleLocal}
                            >
                                Save Offline
                            </Button>
                        </InfoCard>
                    </Grid>
                    <Grid item xs={12} sm={4}>
                        <InfoCard>
                            <h2 className="margin-bottom-0">Save Online</h2>
                            {featureText.saveOnline.desc}

                            <h3 className="margin-bottom-0"><u>Pros &amp; Cons</u></h3>
                            <ul className="fillmid">
                                {
                                    featureText.saveOnline.features.map( t => 
                                        <Li key={t}>{t}</Li>
                                    )
                                }
                            </ul>

                            <Button
                                variant="outlined"
                                color="primary"
                                onClick={saveTaleOnline}
                            >
                                Save Online
                            </Button>
                        </InfoCard>
                    </Grid>
                </Grid>
            </Container>
            {/* <Button
                variant="contained"
                color="primary"
                className="margin-right-15 margin-top-15"
                onClick={exportWIPTale}
            >
                Export this Reactale
            </Button>
            <Button
                variant="contained"
                color="primary"
                className="margin-right-15 margin-top-15"
                onClick={saveTaleLocal}
            >
                Save in local
            </Button>
            <Button
                variant="contained"
                color="primary"
                className="margin-top-15"
                onClick={saveTaleOnline}
            >
                Save Online
            </Button> */}
        </Box>
    )
}

const mapStateToProps = state => ({
    wipTale: state.wipTale
})

export default connect(mapStateToProps)(CreateTaleFinish)